.img {
  display: block;
  margin-left: auto;
  margin-right: auto; }

.redo .img {
  transform: rotate(180deg); }

.redo:hover {
  background: rgba(200, 200, 200, 0.3); }

.undo:hover {
  background: rgba(200, 200, 200, 0.3); }

.disable {
  opacity: 0.1; }

.node circle {
  fill: #fff;
  stroke: steelblue;
  stroke-width: 1.5px;
  cursor: pointer; }

.node text {
  font: 12px sans-serif; }

.node--internal text {
  text-shadow: 0 1px 0 #fff, 0 -1px 0 #fff, 1px 0 0 #fff, -1px 0 0 #fff; }

.link {
  fill: none;
  stroke: #ccc;
  stroke-width: 2px; }

.current {
  fill: #145082 !important; }
