#mid-bar {
  overflow-y: auto;
  overflow-x: auto; }

.used-set-background {
  fill: #f0f0f0;
  stroke: none; }

.used-set-foreground {
  fill: #636363;
  stroke: white;
  stroke-width: 1px; }

.header {
  cursor: pointer; }

.delete-cursor {
  cursor: url(/assets/trash-can.png) 1 1, pointer; }

.connector {
  cursor: s-resize; }

.connector-rect {
  fill: #f0f0f0;
  stroke: none; }

.set-label {
  font-size: 12px; }

.subset-background-rect {
  fill: none;
  stroke: none;
  pointer-events: all; }

.group-background-rect {
  fill: #ccc;
  stroke: #555;
  stroke-width: 1px;
  opacity: 0.3; }

.vert-set-rect {
  fill: none;
  opacity: 0.1; }

.highlight {
  fill: #fed9a6;
  opacity: 1; }

.highlight2 {
  stroke: black;
  stroke-width: 0.5px; }

.set-membership {
  stroke: none; }

.not-member {
  fill: #f0f0f0; }

.member {
  fill: #636363; }

.combination-line {
  stroke: #636363;
  stroke-width: 3px;
  pointer-events: none; }

.cardinality-bar0 {
  fill: #bdbdbd; }

.cardinality-bar1 {
  fill: #888888; }

.cardinality-bar2 {
  fill: #252525; }

.axis {
  stroke: black;
  stroke-width: 1px;
  fill: none;
  shape-rendering: crispEdges; }

.tick {
  stroke: black;
  stroke-width: 1px;
  shape-rendering: crispEdges; }

.tick-label {
  font-size: 0.8em; }

.cardinality-slider-rect {
  fill: #ccc;
  pointer-events: all;
  opacity: 0.5;
  stroke: black;
  stroke-width: 1px;
  cursor: col-resize; }

.break-bar {
  stroke: white;
  stroke-width: 1px; }

.slider-brush {
  fill: #aaa;
  opacity: 0.2;
  pointer-events: none; }

.inner-text-group {
  cursor: s-resize; }

.cardinality-label-rect {
  fill: #ccc;
  stroke: black;
  opacity: 1;
  transition: opacity 1s;
  stroke-width: 0.3px; }

.hide {
  opacity: 0;
  transition: opacity 1s; }

.slider-brush-path {
  fill: #ccc;
  opacity: 0.5;
  transition: opacity 1s; }

.cardinality-text {
  font-size: 0.8em; }

.disproportionality.positive {
  fill: #74add1; }

.disproportionality.negative {
  fill: #f46d43; }

.deviation-label {
  fill: #ccc;
  stroke: black;
  opacity: 1;
  transition: opacity 1s;
  stroke-width: 0.3px; }

.deviation-label:hover {
  fill: #a3a3a3; }

.unselectedAttribute > rect {
  stroke: black;
  stroke-width: 1px; }

.unselectedAttribute > text {
  color: black;
  writing-mode: vertical-lr; }

.whisker {
  stroke: black;
  stroke-width: 0.5px; }

.boxplot-box {
  stroke: black;
  stroke-width: 0.5px;
  fill: #ccc; }

.tick text {
  stroke-width: 0.1px; }

.position-relative {
  position: relative; }

.label {
  stroke: black;
  stroke-width: 1px;
  fill: rgba(120, 120, 120, 0.3); }
