.table-view {
  position: relative;
  width: 100%;
  height: 100%; }

.table-view .table-container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
  padding: 10px; }

table {
  width: 100%;
  border-collapse: collapse; }

/* Zebra striping */
tr:nth-of-type(odd) {
  background: #eee; }

table th {
  background: #333;
  color: white;
  font-weight: bold;
  background-repeat: no-repeat;
  background-position: 3% center; }

td,
th {
  padding: 6px;
  border: 1px solid #ccc;
  text-align: left;
  width: 150px; }

.header-label {
  text-align: center; }

.header-selector .dropdown button {
  min-width: 128px; }
